<template>
  <v-app>
    <Header v-on:tabchange="update" class="font" v-if="theme === 0" />
    <HeaderNE v-on:tabclick="update" v-on:tabchange="update" v-else-if="theme === 1" />
    <v-main style="background-color:	#008080">
  <Projects v-if="tab === 1 && theme === 0"/>
  <ProjectsNE v-on:close="tab = -1" v-if="tab === 1 && theme === 1"/>
  <Contact v-else-if="tab === 2 && theme === 0"/>
  <ContactNE v-on:close="tab = -1" v-else-if="tab === 2 && theme === 1" class="draggable"/>
  <About v-else-if="tab === 0 && theme === 0"/>
  <AboutNE v-on:close="tab = -1" v-else-if="tab === 0 && theme === 1" class="draggable"/> 
  <Profile v-on:close="tab = -1" v-if="tab === 0 && theme === 1" class="draggable"/>
    </v-main>
    <!-- <Footer v-on:changeTheme="changeTheme"/> -->
    <!-- <StartBar /> -->
  </v-app>
</template>

<script>
import About from "./views/About";
import AboutNE from "./views/AboutNE";
import Contact from "./views/Contact";
import ContactNE from "./views/ContactNE";
import ProjectsNE from "./views/ProjectsNE";
import Projects from "./views/Projects";
import Header from "./views/Header";
import HeaderNE from "./views/HeaderNE";
import Profile from "./views/Profile";
//import Draggable from './views/Draggable'
import interact from "interactjs";
//import Footer from './views/Footer'
// import StartBar from './views/StartBar'
export default {
  name: "App",

  components: {
    About,
    AboutNE,
    Contact,
    ContactNE,
    ProjectsNE,
    Projects,
    Header,
    HeaderNE,
    Profile
    //Draggable
    ///Footer,
    // StartBar
  },

  data: () => ({
    tab: 0,
    about_open: true,
    projects_open: false,
    contact_open: false,
    theme: 1
  }),
  methods: {
    update: function(a) {
/*       console.log(a);
      switch (a) {
        case 0:
          this.about_open = true;
          break;
        case 1:
          this.projects_open = true;
          break;
        case 2:
          this.contact_open = true;
          break;
        default:
          break;
      } */

      console.log(a)
      this.tab = a;
    },
    changeTheme: function(a) {
      this.theme = a;
      this.update(0);
    },
    initInteract: function(selector) {
      console.log("init");
      console.log(selector);
      interact(".item")
        .draggable({
          listeners: { move: window.dragMoveListener },
          inertia: false,
          modifiers: [
            interact.modifiers.restrictRect({
              restriction: "parent",
              endOnly: true
            })
          ]
        });
    },
  },
  mounted: function() {
    let aD = this.$refs.aboutDraggable;
    let cD = this.$refs.contactDraggable;
    let pD = this.$refs.projectsDraggable;
    console.log(aD);
    console.log(cD);
    console.log(pD);

    this.initInteract(aD);
    this.initInteract(cD);
    this.initInteract(pD);
  }
};
</script>

